@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
    -webkit-margin-before: 0rem;
            margin-block-start: 0rem;
    -webkit-margin-after: 0rem;
            margin-block-end: 0rem;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}
li {
  text-decoration: none;
  list-style-type: none;
  background-color: lightgrey;
}
a {
  padding: 0.3rem 0.3rem 0.3rem 0.3rem;
  border-radius: 0.25rem;
}
a:hover {
  cursor: pointer;
}
button {
  padding: 0.5rem 1rem 0.5rem 1rem;
  outline:none;
  border: none;
}
/* General */
.screen {
  height: 100vh;
  width: 100%;
  z-index: 9;
  background-color: lightgrey;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.list-container {
  background-color: white !important;
}
.item-container {
  /* border: 1px red dashed; */
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 0 1rem 0 1rem;
}
.pic-container {
  /* border: 1px blue dashed; */
  display: flex;
  justify-content: center;
  align-items: center;
}

div[class*="nav-box"] ul {
    display:flex;
    flex-direction: row;
    align-items: center;
}
div[class*="nav-box"] ul li {
    margin: 0 0.3rem 0 0.3rem;
}
ul[class*="messages-list"] li {
    display: flex;
    justify-content: flex-start;
    padding: 1rem 1rem 1rem 1rem;
    border-bottom: 1px rgb(170, 170, 170) solid;
}
.welcome-statement {
    background-color:rgb(216, 216, 216);
}
ul[class*="add-list"] li:not(:last-child) {
    border-bottom: 1px rgb(170, 170, 170) solid;
}
/* Message */
.message-new {
    height: 0.95rem;
    width: 0.95rem;
    background-color: red;
    color: red;
    border-radius: 0.2rem;
}
.message-received {
    height: 0.6rem;
    width: 0.6rem;
    background-color: none;
    border: 0.17rem solid red;
    border-radius: 0.2rem;
}
.message-opened {
    width: 0.9rem;
	height: 0.9rem;
    background-image: url(/static/media/opened-img-icon.f5c000ed.png);
    background-repeat: no-repeat;
    background-size: 0.9rem 0.9rem;
    background-position: center;
	/* border-top: 0.5rem solid transparent;
	border-left: 1rem solid black;
	border-bottom: 0.5rem solid transparent; */
}
.message-sent {
    width: 0.9rem;
	height: 0.9rem;
    background-image: url(/static/media/sent-img-icon.fee1337a.png);
    background-repeat: no-repeat;
    background-size: 0.9rem 0.9rem;
    background-position: center;
	/* border-top: 0.5rem solid transparent;
	border-left: 1rem solid #664db1;
	border-bottom: 0.5rem solid transparent; */
}
.message-new-friend {
    height: 0.6rem;
    width: 0.6rem;
    background-color: none;
    border: 0.17rem solid lightgrey;
    border-radius: 0.2rem;
}
.message-info {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.message-info > * {
    margin: 0 0.1rem 0 0.1rem;
}
.background-opened-image {
    position: absolute;
    top: 0;
    height: 100vh;
    width: 100%;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
}
.opened-image {
    width: 100%;
}
.webcam-screen {
    height:100vh;
    /* overflow-y: hidden;
    overflow-x: hidden; */
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    flex-direction: column;
}
.webcam-overlay, .captured-overlay {
    height: 100%;
    width:100%;
    position: absolute;
    top:0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.send-overlay {
    height: 100%;
    width:100%;
    position: absolute;
    top:0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: lightgrey;
}
.camera-nav {
    position: absolute;
    top: 0;
    width:100%;
}
.camera-footer, .captured-footer, .send-footer {
    position: absolute;
    bottom: 0;
    width:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.captured-footer {
    flex-direction: row;
    justify-content: space-between;
    background-color: lightgrey;
    padding: 0.5rem 0 1rem 0;
}
.send-footer {
    flex-direction: row;
    justify-content: space-between;
    background-color: lightblue;
    padding: 1rem 0 1rem 0;
}
.captured-overlay {
    background-color: white;
}
.image-desktop {
    width: 100%;
}

/* UI Buttons */
.send-to {
    background-color: yellow;
    border-radius: 1rem;
    margin-right: 0.5rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.send-to-icon {
    height: 0.9rem;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    /* filter: invert(60%); */
    margin-left: 0.3rem;
}
.send-search {
    width: 100%;
    height: 100%;
    outline: none;
    border: 2px solid lightblue;
    background: light-grey;
    border-radius: 1rem;
    padding: 0 1rem 0 1rem;
    margin: 0 1rem 0 1rem;
}
.send-list {
    background-color: white;
}
.selected {
    color: rgb(31, 168, 247);
}
.selected-circle {
    background-color: rgb(31, 168, 247);
    height: 2.5rem;
    width: 2.5rem;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.unselected-circle {
    height: 2.1rem;
    width: 2.1rem;
    border-radius: 100%;
    border: 0.2rem solid lightgrey;
    display: flex;
    justify-content: center;
    align-items: center;
}
.checkmark {
    height:1.7rem;
    width: 1.7rem;
    -webkit-filter: invert(100%);
            filter: invert(100%);
}
.save, .close, .back, .send-label {
    border-radius: 1rem;
    margin-left: 0.5rem;
}
.capture-button {
    width: 7em;
    height: 7em;
    background: none;
    border: 0.5rem solid lightgrey;
    border-radius: 50%;
}

/* FLIPPY */
.flippy-front, .flippy-back {
    padding: 0 !important;
}

.app-nav {
  position: absolute;
  top:0;
  z-index: 10;
  color: black;
  width:100%;
  /* background-color: coral; */
}
/* Navbar section */
.navbar {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    justify-content: space-between;
    height: 2.5rem;
}
.nav-item {
  height: 2.5rem;
  width:2.5rem;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-profile-pic:hover {
  opacity: 80%;
}
.nav-box-1 ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

/* Icons */
.search-icon {
  height: 1rem;
  width: 1rem;
}
.add-friend-icon {
  height: 1rem;
  width: 1rem;
}
.chat-icon {
  height: 1.3rem;
  width: 1.3rem;
}
.flip-icon {
  height: 1.7rem;
  width: 1.7rem;
}

/* Profile Section */
.profile-pic {
  border-radius: 1rem;
  width:8rem;
  margin:1rem
}

/* Add Friend Section */
.friend-profile-pic {
  border-radius: 1rem;
  width:4rem;
}
.friend-info {
  /* border: 1px green dashed; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 0.5rem 0 0.5rem;
}
.friend-button {
  /* border: 1px orange dashed; */
  display: flex;
  align-items: center;
}
.friend-button button {
  border-radius: 1rem;
}
.removed-friend {
  background-color: rgb(255, 126, 126);
}

.app-foot {
  position: absolute;
  bottom:0;
  z-index: 8;
  color: white;
  background-color: lightgrey;
}
.footer {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  justify-content: space-evenly;
  height: 2.5rem;
}
.footer-item {
  height: 2.5rem;
  width:2.5rem;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: none !important;
}
/* UI Icons */
.footer-chat-icon {
  border-radius: 0.25rem;
  height: 2.5rem;
  width: 2.5rem;
  
}
.footer-camera-icon {
  height: 2.3rem;
  width: 2.3rem;
  border-radius: 0.25rem;
}
